import { Component, h, Prop, Host, Event, EventEmitter } from '@stencil/core';
import { isEmpty } from '@springbokagency/stencil-base';
import { ClickActionTarget, ClickActionType, type ClickActionEvent } from './click-action.types';

/**
 * The click action component.
 */
@Component({
  tag: 'bs-click-action',
  styleUrl: 'click-action.scss',
  shadow: false,
})
export class BsClickAction {
  /**
   * The name for the bsClickAction event.
   */
  @Prop() name?: string;

  /**
   * The value for the bsClickAction event.
   */
  @Prop() value?: any;

  /**
   * The type of button for the bsClickAction component.
   */
  @Prop() type?: ClickActionType = ClickActionType.Button;

  /**
   * The href of bsClickAction component, will convert the button into a link.
   */
  @Prop() href?: string;

  /**
   * The target of the provided href.
   */
  @Prop() target?: ClickActionTarget;

  /**
   * The class of the button or link.
   */
  @Prop() tagClass?: string;

  /**
   * Disabled state of inner tag.
   */
  @Prop() disabled?: boolean;

  /**
   * Use aria-label attribute when link/button doesn't have any text
   */
  @Prop() ariaLabelText?: string;

  /**
   * Emitted when the component is clicked.
   */
  @Event() bsClickAction: EventEmitter<ClickActionEvent>;

  private handleClick(event: MouseEvent) {
    const { name, value, type, href, target, bsClickAction } = this;

    if (href && target !== ClickActionTarget.Internal) {
      return;
    }

    bsClickAction.emit({ name, value, type, href, target });
    event.stopPropagation();
    event.preventDefault();
  }

  render() {
    const { type, href, target, disabled } = this;
    const TagType = !isEmpty(href) ? 'a' : 'button';
    const attrs = TagType === 'button' ? { type, disabled } : { href, target, disabled };

    return (
      <Host class="bs-click-action">
        <TagType
          class={this.tagClass}
          {...attrs}
          aria-label={this.ariaLabelText}
          onClick={this.handleClick.bind(this)}
        >
          <slot />
        </TagType>
      </Host>
    );
  }
}
